import React, { useEffect, useRef, useState } from 'react';
import adform from '../assets/adform.png';
import google from '../assets/google.png';
import amazon from '../assets/amazon.png';
import openX from '../assets/openX.png';
import displayBanner1 from '../assets/display_banner_1.png';
import displayBanner2 from '../assets/display_banner_2.png';
import displayBanner3 from '../assets/display_banner_3.png';
import displayBanner4 from '../assets/display_banner_4.png';

function Section2() {
  const tabs = useRef([]);
  const [selectedTab, setSelectedTab] = useState(0);
  const [tabWidth, setTabWidth] = useState(0);

  const addToTabs = (el) => {
    if (el && !tabs.current.includes(el)) {
      tabs.current.push(el);
    }
  }

  useEffect(() => {
    if (Array.isArray(tabs.current) && tabs.current.length > 0) {
      calcTabWidth();
      window.addEventListener('resize', calcTabWidth);
      return () => window.removeEventListener('resize', calcTabWidth);
    }
  }, [tabs]);

  function calcTabWidth() {
    tabs.current.forEach(el => {
      el.style.width = el.parentElement.parentElement.offsetWidth + 'px';
      setTabWidth(el.parentElement.parentElement.offsetWidth);
    });
  }
  return (
    <section className='section2'>
      <div className='wrapper'>
        <div className='title-wrapper'>
          <h2>Transform ideas into high-performing <br /> campaigns, <br /> including but not limited to:</h2>
          <div className='tab-button-wrapper'>
            <button onClick={() => setSelectedTab(0)} className={'button' + (selectedTab === 0 ? ' button-green' : '')}>
              Display
            </button>
            <button onClick={() => setSelectedTab(1)} className={'button' + (selectedTab === 1 ? ' button-yellow' : '')}>
              Social Media
            </button>
            <br />
            <button onClick={() => setSelectedTab(2)} className={'button' + (selectedTab === 2 ? ' button-pink' : '')}>
              CRM
            </button>
          </div>
        </div>
        <div className='tabs'>
          <div className='tab-wrapper' style={{ transform: `translateX(calc(-${tabWidth * selectedTab}px))` }}>
            <div className='tab display-tab' ref={addToTabs}>
              <div className='banners'>
                <div className='banner1 glass-border'></div>
                <div className='banner2 glass-border'></div>
                <div className='banner3 glass-border'></div>
                <div className='banner4 glass-border'></div>
              </div>
              <div className='brands glass-border'>
                <img src={adform} />
                <img src={google} />
                <img src={amazon} />
                <img src={openX} />
                <p>But not limited to</p>
              </div>
            </div>
            <div className='tab social-tab' ref={addToTabs}>
              <div className='banners'>
                <div className='left glass-border'>

                </div>
                <div className='top-bot-wrapper'>
                  <div className='top'>
                    <div className='banner300x250 glass-border'></div>
                    <div className='banner728x90 glass-border'></div>
                  </div>
                  <div className='bot glass-border'></div>
                </div>
              </div>
              <div className='brands glass-border'>
                <img src={adform} />
                <img src={google} />
                <img src={amazon} />
                <img src={openX} />
                <p>But not limited to</p>
              </div>
            </div>
            <div className='tab crm-tab' ref={addToTabs}>
              <div className='banners'>
                <div className='left glass-border'>

                </div>
                <div className='top-bot-wrapper'>
                  <div className='top'>
                    <div className='banner300x250 glass-border'></div>
                    <div className='banner728x90 glass-border'></div>
                  </div>
                  <div className='bot glass-border'></div>
                </div>
              </div>
              <div className='brands glass-border'>
                <img src={adform} />
                <img src={google} />
                <img src={amazon} />
                <img src={openX} />
                <p>But not limited to</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Section2
